.skills {
  h2 {
    font-weight: lighter;
  }

  h3 {
    font-size: 1.2rem;
    font-weight: normal;
  }

  .skill-details {
    column-count: 2;
    column-gap: 5rem;

    @media (max-width: 768px) {
      column-count: initial;
    }

    em {
      font-style: normal;
      font-size: 0.85em;
      color: #999;
    }
  }

  .other {
    > li {
      &:not(:last-of-type):after {
        content: " / ";

      }
    }
  }
}
