.section {
  margin: 0 0 2.5rem 0;

  article {
    margin-bottom: 1.75rem;
    break-before: avoid;

    header {
      h1, h2, h3 {
        font-weight: lighter;

        strong {
          font-weight: 300;
        }
      }

      h1 {
        font-size: 1.75rem;
      }

      h2 {
        font-size: 1.1rem;
        margin-bottom: 1rem;
      }
    }
  }

  > h1 {
    margin-bottom: 1rem;
  }
}

.section-inline {
  margin: 0 0 1rem 0;

  > *:first-of-type {
    display: inline;
  }
}

.section-main {
  margin-bottom: 3rem;
}

